.footer {
	color: $white;
	background: $black;
	.container-fluid {
		padding: 0 41px;
	}
	h5 {
		color: $white;
		font-family: $fontBold, sans-serif;
		font-size: 18px;
		text-transform: uppercase;
		line-height: 40px;
		font-weight: normal;
		margin: 0;
	}
	h6 {
		color: $white;
		font-family: $fontBold, sans-serif;
		font-size: 14px;
		font-weight: normal;
		text-transform: uppercase;
		margin: 0 0 15px 0;
	}
	a {
		font-family: $fontLight, sans-serif;
		font-size: 17px;
		line-height: 24px;
		color: #8f8f8f;
		text-decoration: none;
		&:hover,
		&:focus,
		&:visited {
			color: #ffffff;
		}
		&:visited {
			color: #8f8f8f;
			&:hover {
				color: $white;
			}
		}
	}
	.row-form {
		padding: 38px 0 29px;
		border-bottom: 1px solid rgba($white, 0.2);
		.form-group {
			width: 22.75%;
			float: left;
			margin-bottom: 0;
			& + .form-group {
				margin-left: 3%;
			}
			.btn {
				width: 100%;
			}
		}
		.form-control {
			background: #444444;
			border: none;
			&:focus {
				background: $white;
				color: $black;
			}
		}
	}
	.row-contact {
		padding: 27px 0 33px;
		p {
			font-size: 17px;
			font-family: $fontLight, sans-serif;
			line-height: 24px;
			color: $white;
			margin: 0;
			span {
				color: #8f8f8f;
			}
		}
		.phone {
			text-align: right;
			padding-top: 16px;
			a {
				color: $white;
				font-family: $fontLight;
				font-size: 24px;
				font-weight: 300;
				text-transform: uppercase;
			}
		}
		.images {
			text-align: right;
			a {
				padding-right: 25px;
				display: inline-block;
			}
			.logo {
				display: inline-block;
				font-size: 18px;
				line-height: 1;
				letter-spacing: 2px;
				text-transform: uppercase;
				border-bottom: 1px solid $white;
			}
		}
	}
	.row-bottom {
		text-align: center;
		background: #444444;
		padding: 13px 15px;
		a {
			& + a {
				padding-left: 40px;
			}
		}
	}
	@media (max-width: 479px) {
		.row-bottom {
			a {
				display: block;
				& + a {
					padding: 0;
				}
			}
		}
	}
	@media (max-width: 767px) {
		.container-fluid {
			padding: 0 15px;
		}
		h5 {
			line-height: 24px;
			margin-bottom: 20px;
		}
		.row-form {
			.form-group {
				width: 100%;
				margin-bottom: 15px;
				& + .form-group {
					margin-left: 0;
				}
			}
		}
		.row-contact {
			.phone {
				text-align: center;
				padding-bottom: 20px;
			}
			.images {
				text-align: center;
				a {
					display: block;
					padding: 0 0 20px 0;
				}
			}
		}
	}
	@media (min-width: 768px) and (max-width: 991px) {
		text-align: center;
		h5 {
			margin-bottom: 15px;
		}
		.row-contact {
			.images {
				padding-top: 20px;
			}
		}
	}
	@media (min-width: 992px) and (max-width: 1365px) {
		h5 {
			margin-bottom: 15px;
		}
		.row-contact {
			.images {
				a {
					padding-right: 0;
					margin-bottom: 10px;
					&:first-child {
						padding-right: 15px;
					}
				}
			}
		}
	}
}
