.header {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 100;
	.row {
		margin: 0;
	}
	.col {
		float: left;
	}
	.navbar-toggle {
		margin: 0;
		padding: 18px 17px 16px 18px;
		float: none;
		.icon-bar {
			background: $black;
			width: 21px;
			height: 2px;
			display: block;
			& + .icon-bar {
				margin-top: 4px;
			}
		}
	}
	.navbar-nav {
		float: none;
		& > li {
			padding: 0 13px;
			& > a {
				font-size: 12px;
				text-transform: uppercase;
				padding: 6px 0;
				border-style: solid;
				border-width: 0 0 1px 0;
				border-color: transparent;
				color: $white;
				line-height: 1;
				letter-spacing: 0.8px;
				&:hover,
				&:focus,
				&:visited {
					color: $white;
					background: none;
					outline: none;
				}
				&:hover,
				&:focus {
					border-color: $white;
				}
			}
		}
	}
}
.logo {
	h1 {
		text-transform: uppercase;
		margin: 0;
		padding: 0;
		letter-spacing: 1.5px;
		line-height: 1;
		border-style: solid;
		border-width: 0 0 1px 0;
	}
}
.phone {
	color: $black;
	a {
		color: $black;
		&:hover,
		&:focus,
		&:visited {
			color: $black;
			text-decoration: none;
		}
	}
}
@media (max-width: 1365px) {
	.header {
		//position: relative;
		padding: 0 0 0 18px;
		background: $white;
		.logo {
			h1 {
				color: $black;
				font-size: 14px;
				padding: 17px 0 4px 0;
				border-color: $black;
			}
		}
		.phone {
			padding: 13px 0 0;
			font-size: 25px;
		}
		.phone,
		.btn-menu {
			float: right;
		}
		.menu {
			position: absolute;
			left: 0;
			top: 100%;
			width: 100%;
			background: $white;
		}
		.navbar-nav {
			& > li {
				&  > a {
					color: $black;
					&:hover,
					&:focus,
					&:visited {
						color: $black;
					}
				}
			}
		}
	}
}

@media (min-width: 1366px) {
	.header {
		padding: 24px;
		.logo {
			width: 14%;
			h1 {
				color: $white;
				font-size: 18px;
				border-color: $white;
				padding: 13px 0 4px 0;
				display: inline-block;
			}
		}
		.menu {
			width: 55%;
			padding-top: 10px;
		}
		.phone {
			width: 15%;
			font-size: 20px;
			color: $white;
			letter-spacing: 0.8px;
			padding: 10px 15px 0 0;
			a {
				color: $white;
				&:hover,
				&:focus,
				&:visited {
					color: $white;
				}
			}
		}
		.btn-box {
			width: 16%;
		}
		.navbar-nav {
			display: inline-block;
		}
		.menu,
		.phone,
		.btn-menu {
			text-align: center;
		}
	}
}
