html,
body,
main {
	height: 100%;
}

body {
	font-family: $fontRegular, sans-serif;
	font-size: 18px;
	line-height: 1;
}

.wrapper {
	min-height: 100%;
	position: relative;
	overflow: hidden;
}

.btn {
	border: none;
}
.btn-default {
	font-size: 12px;
	font-family: $fontBold, sans-serif;
	color: $black;
	background: $white;
	border: 2px solid $white;
	text-transform: uppercase;
	line-height: 1;
	padding: 14px 22px;
	&:hover,
	&:focus {
		background: $white;
		border-color: $white;
	}
	@media (max-width: 767px) {
		font-size: 11px;
		background: transparent;
		color: $white;
		&:hover,
		&:focus {
			background-color: $white;
			border-color: $white;
			color: $black;
		}
	}
}
.btn-primary {
	background: $black;
	color: $white;
	font-size: 12px;
	text-transform: uppercase;
	padding: 11px 30px;
	&:hover {
		color: $black;
		background: $white;
	}
}
.btn-black {
	border: 2px solid $black;
	color: $black;
	padding: 12px 22px;
	&:hover,
	&:focus {
		background: $black;
		color: $white;
	}
}

.form-control {
	font-family: $fontLight;
	font-size: 17px;
	line-height: 24px;
	color: $white;
	border-width: 0 0 1px 0;
	background: none;
	border-color: $white;
	box-shadow: none;
	padding: 8px 12px;
	height: auto;
	&:hover,
	&:focus,
	&:active,
	&:focus:active {
		outline: none;
		box-shadow: none;
		border-color: $white;
		border-width: 0 0 1px 0;
	}
}
::-webkit-input-placeholder { /* Chrome/Opera/Safari */
	color: rgba($white, 0.5);
}
::-moz-placeholder { /* Firefox 19+ */
	color: rgba($white, 0.5);
}
:-ms-input-placeholder { /* IE 10+ */
	color: rgba($white, 0.5);
}
:-moz-placeholder { /* Firefox 18- */
	color: rgba($white, 0.5);
}
.bootstrap-select {
	.btn {
		font-family: $fontLight, sans-serif;
		padding: 8px 12px;
		font-size: 17px;
		line-height: 24px;
		text-transform: none;
		border-width: 0 0 1px 0;
		border-style: solid;
		border-color: $white;
	}
	& > .dropdown-toggle.bs-placeholder,
	& > .dropdown-toggle.bs-placeholder:hover,
	& > .dropdown-toggle.bs-placeholder:focus,
	& > .dropdown-toggle.bs-placeholder:active {
		color: $white;
		background: transparent;
		box-shadow: none;
		border-bottom-color: $white;
	}
	.btn .caret {
		border-top: 7px dashed;
		border-top: 7px solid $white;
		border-right: 7px solid transparent;
		border-left: 7px solid transparent;
	}
	& > .dropdown-toggle {
		background: transparent;
	}
	& > .dropdown-toggle.bs-placeholder {
		color: rgba($white, 0.5);
	}
	.dropdown-menu {
		margin: 6px 0 0;
		left: -10px;
		right: -10px;
		padding: 0;
		& > li {
			& + li {
				a {
					border-top: 1px solid #b3d179;
				}
			}
			& > a {
				color: $black;
				font-size: 17px;
				line-height: 24px;
				font-family: $fontLight;
				padding: 13px 21px;
				&:hover,
				&:focus {
					background: #b3d179;
					color: $white;
					outline: none;
				}
			}
		}
	}
}



.section {
	min-height: 100vh;
	border-bottom: 1px solid rgba($black, 0.1);
	h3 {
		color: $black;
		font-size: 36px;
		font-family: $fontBold, sans-serif;
		line-height: 48px;
		text-transform: uppercase;
		margin: 0 0 10px 0;
	}
	p {
		color: $textColor;
		font-size: 17px;
		font-weight: 300;
		line-height: 24px;
		margin-bottom: 40px;
	}
	.desc {
		p {
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
}
.section-intro {
	background: url('../img/bg-intro.jpg') no-repeat center center / cover;
	height: 100vh;
	color: $white;
	position: relative;
	border-bottom: none;
	&:before {
		content: "";
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		background: rgba($black, 0.1);
		z-index: 1;
	}
	.container {
		height: 100%;
		min-height: 100vh;
		position: relative;
		z-index: 2;
	}
	.content {
		position: relative;
		top: 45%;
		transform: translate(0, -50%);
	}
	p {
		color: $white;
	}
	a {
		color: $white;
		&:hover,
		&:focus,
		&:active {
			color: $white;
		}
	}
	.link,
	.image {
		position: absolute;
	}
	.link {
		bottom: 39px;
		left: 15px;
		opacity: 0.5;
	}
	.image {
		right: 28px;
		bottom: 28px;
	}
	h2 {
		font-family: $fontBold, sans-serif;
		font-size: 48px;
		line-height: 1;
		margin: 0 0 30px 0;
		text-transform: uppercase;
	}
	.desc {
		margin-bottom: 30px;
		p {
			font-family: $fontBold, sans-serif;
			font-size: 21px;
		}
	}
	@media (max-width: 767px) {
		.container {
			padding: 0 25px;
		}
		.content {
			padding-top: 72px;
			top: 0;
			transform: translate(0);
		}
		h2 {
			font-size: 38px;
			margin-bottom: 13px;
		}
		.desc {
			margin-bottom: 21px;
			p {
				font-size: 17px;
			}
		}
		.link {
			bottom: 17px;
			left: 26px;
			opacity: 1;
			font-size: 14px;
			a {
				text-decoration: underline;
				&:hover {
					text-decoration: none;
				}
			}
		}
		.image {
			right: 20px;
			bottom: 20px;
			width: 37px;
			height: 41px;
			img {
				width: 100%;
			}
		}
	}
	@media (max-height: 480px) {
		height: auto;
		.container {
			height: auto;
		}
		.content {
			padding: 80px 0;
			top: 0;
			transform: translate(0);
		}
	}

}
.section-about {
	padding: 70px 0 15px;
	position: relative;
	&:before {
		content: "";
		position: absolute;
		top: 0;
		right: 0;
		width: 50%;
		height: 100%;
		background: url("../img/bg-about.png") no-repeat right bottom / auto;
	}
	.desc {
		margin-bottom: 30px;
	}
	.btn-primary {
		width: 220px;
	}
	.about-list {
		padding-top: 45px;
		.image {
			display: block;
			width: 69px;
			height: 69px;
			background-position: left center;
			background-repeat: no-repeat;
			background-size: auto;
			margin-bottom: 15px;
			&.i-1 {
				background-image: url('../img/about-1.png');
			}
			&.i-2 {
				background-image: url('../img/about-2.png');
			}
			&.i-3 {
				background-image: url('../img/about-3.png');
			}
			&.i-4 {
				background-image: url('../img/about-4.png');
			}
			&.i-5 {
				background-image: url('../img/about-5.png');
			}
			&.i-6 {
				background-image: url('../img/about-6.png');
			}
		}
		.text {
			display: block;
			height: 44px;
			text-transform: uppercase;
			font-size: 14px;
			line-height: 16px;
			font-family: $fontBold, sans-serif;
			margin-bottom: 30px;
		}
	}
	@media (max-width: 991px) {
		&:before {
			width: 0;
		}
	}
	@media(min-width: 992px) and (max-width: 1365px) {
		&:before {
			width: 30%;
			background-position: left center;
		}
	}
}
.section-specifications {
	position: relative;
	&:before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 50%;
		height: 100%;
		background: url('../img/bg-section-specifications.jpg') no-repeat center center / cover;
		z-index: 1;
	}
	&:after {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 50%;
		height: 100%;
		background: rgba($black, 0.2);
		z-index: 2;
	}
	.container {
		z-index: 3;
		position: relative;
	}
	.right {
		.content {
			padding: 20% 15px;
		}
	}
	.specifications-list {
		border: 1px solid #ffffff;
		background-color: rgba(255, 255, 255, 0.1);
		margin: 0;
		li {
			height: 150px;
			color: $white;
			font-size: 14px;
			line-height: 1;
			font-family: $fontBold, sans-serif;
			text-transform: uppercase;
			padding: 18px;
			text-align: center;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			border-style: solid;
			border-color: rgba($white, 0.3);
			border-width: 0;
			& + li {
				border-width: 1px 0 0;
			}
			span {
				font-size: 48px;
				font-family: $fontRegular;
				margin-bottom: 10px;
				display: block;
				.small {
					font-size: 14px;
					display: inline-block;
				}
			}
		}
	}
	@media (max-width: 767px) {
		h3 {
			font-size: 25px;
		}
		.right {
			.content {
				padding-left: 0;
				padding-right: 0;
			}
		}
	}
	@media (min-width: 768px) {
		.specifications-list {
			li {
				width: 50%;
				float: left;
				&:first-child {
					border-width: 0 1px 1px 0;
				}
				&:last-child {
					border-width: 1px 0 0 1px;
				}
			}
		}
	}
	@media (max-width: 991px) {
		&:before,
		&:after {
			content: none;
		}
		.container {
			width: 100%;
		}
		.left {
			padding-top: 10%;
			padding-bottom: 10%;
			&:before,
			&:after {
				content: "";
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
			}
			&:before {
				background: url('../img/bg-section-specifications.jpg') no-repeat center center / cover;
				z-index: 0;
			}
			&:after {
				background: rgba($black, 0.2);
				z-index: 1;
			}
			.specifications-list {
				position: relative;
				z-index: 3;
			}
		}
	}
	@media (min-width: 992px) {
		display: flex;
		align-items: center;
		.container {
			.row {
				display: flex;
				align-items: center;
			}
		}
		.right {
			.content {
				padding: 10% 15px;
			}
		}
	}
	@media (min-width: 1366px) {
		.left {
			padding: 0 50px 0 0;
			margin-left: -35px;
		}
		.right {
			padding-left: 35px;
			.content {
				padding-left: 57px;
			}
		}
	}
}
.section-mortgage {
	padding-top: 105px;
	padding-bottom: 50px;
	h3 {
		margin-bottom: 40px;
	}
	.mortgage-list {
		overflow: hidden;
		border-style: solid;
		border-color: $borderColor;
		border-width: 1px 1px 0 1px;
		margin: 0 0 55px 0;
	}
	.item {
		padding: 15px;
		margin-left: -15px;
		margin-right: -15px;
		height: 170px;
		display: flex;
		align-items: center;
		justify-content: center;
		border-style: solid;
		border-color: $borderColor;
		border-width: 0 1px 1px 0;
		img {
			max-width: 100%;
		}
	}
}
.section-gallery {
	position: relative;
	min-height: inherit;
	.container-gallery {
		position: absolute;
		top: 80px;
		right: 0;
		left: 0;
		margin: auto;
		z-index: 1000;
		h3 {
			color: $white;
		}
	}
	.item {
		img {
			width: 100%;
		}
	}
	.gallery-slider-controls {
		position: absolute;
		bottom: 0;
		left: 0;
		z-index: 1000;
		background: $white;
		ul {
			padding: 0;
			list-style: none;
			margin: 0;
			li {
				float: left;
				cursor: pointer;
				border-style: solid;
				border-color: $white;
				border-width: 1px 1px 0 0;
				&.active {
					opacity: 0.5;
				}
			}
		}
	}
	@media (max-width: 767px) {
		.container-gallery {
			padding-top: 20px;
			position: static;
			h3 {
				color: $black;
			}
		}
	}
	.lSAction {
		& > a {
			&.lSPrev,
			&.lSNext {
				background: #000;
				width: 60px;
				height: 72px;
				margin-top: -30px;
				line-height: 1;
				color: $white;
				opacity: 1;
				text-align: center;
				font-size: 38px;
				i {
					line-height: 72px;
				}
			}
		}
	}
	.lSAction > .lSPrev {
		left: 0;
	}
	.lSAction > .lSNext {
		right: 0;
	}
}
.section-map {
	.map {
		height: 100vh;
	}
}
.table {
	margin: 0;
	& > thead,
	& > tbody {
		& > tr {
			& > th,
			& > td {
				line-height: 24px;
				color: #555555;
				text-align: center;
				a {
					color: $green;
					&:hover,
					&:active,
					&:focus {
						color: $green;
					}
					&.icon {
						display: inline-block;
						line-height: 24px;
						font-size: 5px;
						padding-left: 5px;
						vertical-align: top;
					}
				}
			}
		}
	}
	& > thead {
		& > tr {
			& > th {
				font-size: 14px;
				font-family: $fontBold, sans-serif;
				font-weight: normal;
				text-transform: uppercase;
				border: none;
				padding: 23px 8px;
				white-space: nowrap;
				border-bottom: 1px solid rgba(#777777, 0.2);;
			}
		}
	}
	& > tbody {
		& > tr {
			&:hover {
				background: $green;
				& > td {
					color: $white;
					cursor: pointer;
					a {
						color: $white;
					}
				}
			}
			& > td {
				border: none;
				font-size: 17px;
				font-family: $fontLight, sans-serif;
				padding: 13px 8px;
			}
			&:nth-of-type(2n+1) {
				background: #f7f7f7;
				&:hover {
					background: $green;
				}
			}
			&.no-data {
				height: 500px;
				background: transparent !important;
				color: #555555;
				vertical-align: middle;
				&:hover {
					background: transparent;
				}
				.image {
					margin-bottom: 20px;
					opacity: 0.1;
				}
			}
		}
	}
}
.section-filter {
	position: relative;
	padding: 70px 0 55px;
	h3 {
		color: $white;
		margin-bottom: 33px;
	}
	.filter-form {
		.form-content {
			border-color: $white;
			border-style: solid;
			border-width: 1px 1px 0 1px;
			margin-bottom: 40px;
		}
		.form-group {
			border-bottom: 1px solid $white;
			padding: 29px 15px 29px 30px;
			margin: 0;
		}
		.form-control {
			display: inline-block;
			width: auto;
			text-align: center;
			&.bootstrap-select {
				width: 244px;
				text-align: left;
			}
			&.squareFrom,
			&.squareTo {
				width: 64px;
			}
			&.priceFrom,
			&.priceTo {
				width: 94px;
			}
		}
		.label {
			font-size: 17px;
			font-family: $fontLight, sans-serif;
			font-weight: 300;
			line-height: 40px;
			padding: 0 10px 0 0;
			margin: 0;
			display: inline-block;
			&.label-center {
				padding-right: 10px;
				padding-left: 10px;
			}
		}
		.btn-filter {
			margin-right: 20px;
			padding: 22px 22px 14px 22px;
			font-size: 14px;
			font-family: $fontRegular, sans-serif;
			float: left;
		}
		.btn-refresh {
			padding: 14px 17px;
			line-height: 16px;
			float: right;
			font-size: 19px;
		}
	}
	.table-wrap {
		border: 1px solid rgba(#777777, 0.2);
		background-color: rgba(0, 0, 0, 0);
		overflow: auto;
	}
	@media (max-width: 991px) {
		padding-top: 0;
		h3 {
			font-size: 30px;
		}
		.left {
			padding-top: 50px;
			padding-bottom: 50px;
			background: $green;
		}
		.right {
			padding-top: 50px;
		}
	}
	@media (min-width: 992px) {
		&:before {
			content: "";
			background: $green;
			width: 33.33333%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
		}
	}
	@media (min-width: 992px) and (max-width: 1365px) {
		h3 {
			font-size: 30px;
		}
		.filter-form{
			.label-title {
				display: block;
				text-align: left;
			}
			.form-control.priceFrom,
			.form-control.priceTo {
				width: 80px;
			}
		}
	}
	@media(min-width:1366px) {
		&:before {
			width: 34.35%;
		}
		.left {
			width: 34.35%;
			padding: 0 50px;
		}
		.right {
			width: 65.65%;
			padding: 4px 0 0 54px;
		}
		.filter-form-wrap {
			width: 368px;
			float: right;
		}
		.btn-filter {
			width: 290px;
		}
		.table-wrap {
			width: 790px;
			float: left;
		}
	}
}
.modal {
	.modal-dialog {
		width: 370px;
	}
	.modal-content {
		position: relative;
	}
	.modal-header {
		padding: 40px 40px 15px;
		border-bottom: none;
		.modal-title {
			font-size: 18px;
			font-family: $fontBold;
			text-transform: uppercase;
			line-height: 1;
			letter-spacing: 0.9px;
		}
	}
	.modal-body {
		padding: 15px 40px 40px;
		.form-group {
			margin: 0;
			& + .form-group {
				margin-top: 10px;
			}
		}
		.form-control {
			border: 1px solid #d5d5d5;
			padding: 7px 12px;
			&:focus {
				border-color: $black;
			}
		}
		p {
			font-size: 17px;
			line-height: 24px;
			font-family: $fontLight;
			margin-bottom: 20px;
			&.desc {
				padding: 10px 0 16px;
				margin: 0;
				color: #8f8f8f;
			}
		}
		.btn {
			width: 100%;
			text-align: center;
		}
	}
	&.big {
		.modal-dialog {}
	}
	@media (max-width: 	767px) {
		.modal-dialog {
			width: 90%;
			margin: 50px auto 0;
		}
	}
	@media (min-width: 	768px) {
		&.big {
			.modal-dialog {
				width: 700px;
			}
			.modal-header {
				.modal-title {
					font-size: 36px;
					line-height: 1;
				}
			}
		}
	}
	@media (min-width: 992px) {
		&.big {
			.modal-dialog {
				width: 770px;
			}
		}
	}
	@media (max-width: 991px) {
		.modal-content {
			&:before,
			&:after {
				content: "";
				width: 50px;
				height: 50px;
				top: -25px;
				right: -25px;
				position: absolute;
				//z-index: 10001;
			}
			&:before {
				border-radius: 50%;
				background: rgba(#000, 0.5);
			}

			&:after {
				background: url('../img/close.png') no-repeat center center / auto;
			}
		}
	}
}
.modal-backdrop {
	background: rgba(#000, 0.5);
	cursor: pointer;
	&.in {
		opacity: 1;
	}
	@media (min-width: 992px) {
		&:before,
		&:after {
			content: "";
			width: 50px;
			height: 50px;
			top: 30px;
			right: 30px;
			position: absolute;
			z-index: 1000;
		}
		&:before {
			border-radius: 50%;
			background: rgba(#000, 0.5);
		}

		&:after {
			background: url('../img/close.png') no-repeat center center / auto;
		}
	}
}
