@font-face
	font-family: "svgfont"
	src: url('fonts/svgfont.eot')
	src: url('fonts/svgfont.eot?#iefix') format('eot'), url('fonts/svgfont.woff') format('woff'), url('fonts/svgfont.woff2') format('woff2'), url('fonts/svgfont.ttf') format('truetype')
	font-weight: normal
	font-style: normal

$icon-left: "\EA01"
$icon-right: "\EA02"
$icon-phone: "\EA03"
$icon-refresh: "\EA04"
$icon-download: "\EA05"
$icon-arrow-up: "\EA06"
$icon-mouse: "\EA07"
$icon-bg-filter: "\EA08"


%icon
	font-family: "svgfont"
	font-style: normal
	font-weight: normal
	text-rendering: auto
	speak: none
	line-height: 1
	-webkit-font-smoothing: antialiased
	-moz-osx-font-smoothing: grayscale

.icon, [class^="icon-"], [class*=" icon-"]
	@extend %icon
	display: inline-block

.icon-left:before
	content: $icon-left
.icon-right:before
	content: $icon-right
.icon-phone:before
	content: $icon-phone
.icon-refresh:before
	content: $icon-refresh
.icon-download:before
	content: $icon-download
.icon-arrow-up:before
	content: $icon-arrow-up
.icon-mouse:before
	content: $icon-mouse
.icon-bg-filter:before
	content: $icon-bg-filter